.remodal-overlay {

    // background: rgba( 160, 159, 158, 0.8) !important;
}


.remodal-first {
    padding-top: 95px;
    text-align: center;
    width: 376px;

    @media @mobile {
        max-width: 376px !important;
        height: auto;
        padding: 40px 15px;
        margin: 0 auto;
        text-align: center;
        background: #f9fbfc;
        border-radius: 10px;
        
    }
    @media @tablet {

        padding: 50px 15px;
        max-width: 489px !important;
        height: 487px;
        text-align: left;
        border-radius: 0px;
        background: url('@{dir}form.png') no-repeat top center;
        padding: 120px 0 0 109px;

    }

    .remodal-close {

        @media @mobile {
            color: #000 !important;
        }
        @media @tablet {
            color: #fff !important;

        }
    }
    &:before {
        display: none;
    }
}





.btn-remodal {
    margin: 30px 0 0 15px;

}






.remodal-close { 
    top: 12px !important;
    right: 15px !important;
    color: #fff !important;
    font-weight: 900;
    @media @mobile {
       width: 10px !important;
       height: 16px !important;
   }
   @media @tablet { 
    width: 35px !important;
    height: 30px !important;
}
&:before {
    @media @mobile {
        font-size: 30px !important;
        top: -7px !important;
        left: -12px !important;
    }
    @media @tablet { 
        font-size: 50px !important;
        top: -0px !important;
        left: -0px !important;
    }
}
}

.remodal-common {

    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    border-radius: 15px;
    background: #fff;

    //  background: url('@{dir}product-1.png') no-repeat top center;  
    // background-position: 50% 50%;

    padding: 30px 40px;

    
    // background: #3d4f4a;
    // border: 2px solid #fff;
    

    @media @mobile {
        max-width: 500px !important;
        padding: 40px 15px;
        margin: 0 auto;
        text-align: center;
        
    }
    @media @tablet {
        padding: 40px 20px;
        max-width: 450px !important;
        text-align: center;
    }
    .remodal-close {
        color: #000 !important;
    }
}



.error-input{
    clear: both;
    float: left;
    font-size: 13px;
    padding-left: 93px;
    color: #df3a3a;

    margin: 0 auto;
    text-align: center;
    
}


input:focus:required:invalid,
textarea:focus:required:invalid {
  // border: solid 0px rgba(222,78,0,.51) !important;
  border-bottom: 1px solid #909091;
  
}
input:required:valid,
textarea:required:valid {
  // border: solid 0px rgba(222,78,0,.51) !important;
  border-bottom: 1px solid #909091;
  
}
#name-error {
    font-size: 0 !important;
}
.input {
    position: relative;
}
label {
    position: absolute;
    top: 0;
    border: 0;
    font-size: 0 !important;
    vertical-align: baseline;
    margin: 0;
    padding: 0;
}





.formBox {
    position: relative;
    text-align: center;
    border-top: 3px solid @yellow;
    border-radius: 10px;
    background-image: -moz-linear-gradient( 90deg, rgb(133,63,199) 10%, rgb(126,67,177) 100%);
    background-image: -webkit-linear-gradient( 90deg, rgb(133,63,199) 10%, rgb(126,67,177) 100%);
    background-image: -ms-linear-gradient( 90deg, rgb(133,63,199) 10%, rgb(126,67,177) 100%);
    box-shadow: inset 0px 3px 0px 0px rgba(255, 186, 0, 0.004);
    z-index: 10;

    position: relative;
    padding: 30px 15px;
    max-width: 400px;
    margin: 20px auto;


    .form__title {
        padding-top: 22px;
        padding-bottom: 10px;
        font-size: 24px;
        font-weight: 600;
        text-align: center;
        text-transform: uppercase;
    }

    .form__text {
        display: flex;
        justify-content: center;

        .text {
            font-size: 18px;
            padding-right: 10px;
            color: #b3a6bf;
        }
    }


   .form__line {

        width: 85%;
        height: 48px;
        padding: 0 45px;
        color: #b3a6bf;
        border: none!important;
        background-color: rgb(108, 55, 154);
        box-shadow: inset 0px 1px 0px 0px rgba(102, 52, 145, 0.004);
    }

    .inputWrap1 {
        position: relative;
        &:before {
            content: '\e803';
            position: absolute;
            font-family: 'Fontello';
            top: 38px;
            left: 13%;
            font-size: 15px;
            z-index: 10;
            color: @yellow;
        }
    }
    .inputWrap2 {
        position: relative;
        &:before {
            content: '\e804';
            position: absolute;
            font-family: 'Fontello';
            top: 15px;
            left: 13%;
            font-size: 17px;
            z-index: 10;
            color: @yellow;
        }
    }

    .name {
        position: relative;

        margin-top: 23px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px; 
    }
    .phone-mask {
        margin-bottom: 23px;
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px; 
    }

    .btn__wrap {
        width: 230px;
        height: 42px;
        border-radius: 21px;    
        position: relative;
        z-index: 10;
        overflow: hidden;
        margin: auto;
        &:before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 0;
            height: 100%;
            background: @yellow;
            transition: all .5s linear;
            border-radius: 21px;    
            z-index: -1;
        }

        &:hover {
            &:before {
                width: 100%;
            }
        }
    }

    .btn {
        width: 230px;
        height: 42px;
        font-size: 16px;
        font-weight: 900;
        font-family: @ff2;
        text-transform: uppercase;

        border: 3px solid @yellow;
        border-radius: 21px;            
    }
}

.video {
    max-width: 780px;
    iframe {
        border: none;
        width: 100%;
        height: 100%;
    }
}

.thanks__title {
